body{
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sians-serf;
}
ul,li{
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.container{
  /*width: 96%;*/
  width:1004px;
  margin: 0 auto;
}
.text-center{
  text-align: center;
}
.text-left{
  text-align: left;
}
.text-right{
  text-align: right;
}
.row{
  overflow: hidden;
}
[class^=col-]{
  float: left;
  margin-left: 1%;
}

.col-10{
  width: 8%;
}
.col-20{
  width: 18%;
}
.col-25{
  width: 23%;
}
.col-30{
  width:28%;
}
.col-33{
  width: 31.33%;
}
.col-50{
  width: 48%;
}
.col-60{
  width: 58%;
}
.col-70{
  width:68%;
}
.col-80{
  width:78%;
}
.col-90{
  width:88%;
}



